    height: 25px;
}

.gh-foot-mast-text {
    margin: 10px 0;
    color: var(--midgrey);
    line-height: 1.5em;
}

.gh-langswitch .gh-drop-trigger {
    display: inline-block;
    padding: 4px 8px 4px 0;
    color: color-mod(var(--blue) l(-5%));
}

.gh-langswitch .gh-drop-trigger span {
    display: flex;
    align-items: center;
}

.gh-langswitch svg {
    margin-left: 4px;
    width: 7px;
    height: auto;
}

.gh-langswitch .gh-dropdown {
    right: -65px;
    width: 130px;
}

.gh-langswitch .gh-dropdown .gh-badge-outline {
    transition: all 0.4s ease;
}

.gh-langswitch a:hover .gh-badge-outline {
    border-color: color-mod(var(--darkgrey) l(+15%));
    color: color-mod(var(--darkgrey) l(+15%));
}

.gh-langswitch .active a {
    color: var(--blue);
}

.gh-langswitch .active .gh-badge-outline,
.gh-langswitch .active a:hover .gh-badge-outline {
    border-color: var(--blue);
    color: var(--blue);
}

