/* button */

/* Buttons
/* ---------------------------------------------------------- */

.gh-button {
    display: inline-block;
    min-width: 75px;
    height: var(--height);
    padding: 0 2rem;
    border: 0;
    border-radius: var(--radius);
    cursor: pointer;
    font-family: var(--font-sans-serif);
    font-size: 1.4rem;
    font-weight: var(--font-normal);
    line-height: var(--height);
    text-align: center;
    text-decoration: none;
    white-space: nowrap;
    appearance: none;
    transition: 0.4s ease;
}

.gh-button.gh-button-fit {
    width: 100%;
}

.gh-button.gh-button-small {
    height: calc(var(--height) * 0.9);
    line-height: calc(var(--height) * 0.9);
    padding: 0 1.5rem;
    font-size: 1.2rem;
}

.gh-button.gh-button-large {
    height: calc(var(--height) * 1.2);
    line-height: calc(var(--height) * 1.2);
    padding: 0 2.5rem;
    font-size: 1.7rem;
}

.gh-button.gh-button-disabled,
.gh-button:disabled {
    pointer-events: none;
    opacity: 0.4;
}

.gh-button {
    color: var(--color-primary) !important;
    background-color: transparent;
    box-shadow: inset 0 0 0 2px var(--color-primary);
}

.gh-button:hover {
    text-decoration: none;
    color: color(var(--color-primary) l(-15%)) !important;
    box-shadow: inset 0 0 0 2px color(var(--color-primary) l(-10%));
    transition: 0.2s ease;
}

.gh-button.gh-button-primary {
    color: #fff !important;
    background-color: var(--color-primary);
    box-shadow: none;
}

.gh-button.gh-button-primary:hover {
    background-color: color(var(--color-primary) l(-10%));
}

/* more button */
.gh-btn {
    display: inline-block;
    outline: none;
    border: 1px solid #cbdfeb;
    color: #738a94;
    text-decoration: none !important;
    user-select: none;
    fill: #738a94;
    border-radius: 5px;
    transition: none;
    -webkit-font-smoothing: subpixel-antialiased;
}

.gh-btn span {
    display: block;
    padding: 0 14px;
    height: 37px;
    font-size: 1.3rem;
    line-height: 37px;
    font-weight: 400;
    text-align: center;
    border-radius: 4px;
}

.gh-btn.disabled,
.gh-btn[disabled],
fieldset[disabled] .gh-btn {
    box-shadow: none;
    opacity: 0.8;
    cursor: not-allowed;
    pointer-events: none;
}

.gh-btn-blue {
    padding: 1px;
